<template>
  <div class="container-fluid">
    <div class="container">
      <div>
        <div class="card card-muni card-muni-min">
          <div :style="{display: styleDisplayCambio}">
            <h3>Activación de Cuenta</h3>
            <div class="row justify-content-center">
              <div class="col-12 col-sm-6 col-md-12">
                <div class="mt-1 line15">
                  <label>Ingrese su nueva contraseña:</label>
                </div>
              </div>
              <div class="col-12 col-sm-6 col-md-12">
                <input
                  type="password"
                  class="form-control input-number"
                  id="nuevopass"
                  name="nuevopass"
                  required
                  v-model="nuevaClave"
                  maxlength="15"
                />
              </div>
            </div>
            <div class="row justify-content-center">
              <div class="col-12 col-sm-6 col-md-12">
                <div class="mt-1 line15">
                  <label>Ingrese nuevamente su contraseña:</label>
                </div>
              </div>
              <div class="col-12 col-sm-6 col-md-12">
                <input
                  type="password"
                  class="form-control input-number"
                  id="renuevopass"
                  name="renuevopass"
                  required
                  v-model="repiteClave"
                  maxlength="15"
                />
              </div>
            </div>
            <div class="row justify-content-center">
              <div class="col-12 col-sm-12 col-md-12 text-center mt-3">
                <button
                  class="btn btn-block btn-lg btn-primary"
                  @click="CambiarPass()"
                >Activar Cuenta</button>
              </div>
            </div>
          </div>
          <div class="text-center activacion-correcta" :style="{display: styleDisplayCorrecto}">
            <div>
              <img src="../../assets/success.png" alt />
            </div>
            <h4>
              Su cuenta se ha activado correctamente
              <br />
            </h4>
            <button
              class="btn btn-block btn-lg btn-primary"
              v-bind:style="{display: styleBtnIniciarSesion}"
              @click="IniciarSesion()"
            >Iniciar Sesion</button>
          </div>
          <div class="no-existe" v-bind:style="{display: styleDisplayNoExiste}">
            <h4>Ups!</h4>
            <h5>La pagina solicitada no existe</h5>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.no-existe {
  text-align: center;
  h4 {
    color: #21c0f3;
    font-size: 45px;
    font-weight: bold;
  }
  h5 {
    color: #21c0f3;
  }
}
.activacion-correcta {
  h4 {
    color: #21c0f3;
    padding-top: 20px;
    padding-bottom: 40px;
  }
}
</style>
<script>
import axios from "axios";
import Constantes from "../../store/constantes.js";
export default {
  data() {
    return {
      nuevaClave: "",
      repiteClave: "",
      styleDisplayCambio: "",
      styleDisplayCorrecto: "none",
      styleDisplayNoExiste: "none",
      styleBtnIniciarSesion: "none",
      hash: ""
    };
  },
  mounted() {
    this.ValidaHash();
  },
  methods: {
    ValidaFormulario() {
      var valida = true;
      if (this.nuevaClave == "") {
        this.$swal({
          icon: "error",
          title: "Error",
          text: "Debe digitar la nueva contraseña"
        });
        valida = false;
      }
      if (this.repiteClave != this.nuevaClave) {
        this.$swal({
          icon: "error",
          title: "Error",
          text: "Las contraseñas no coinciden"
        });
        valida = false;
      }
      return valida;
    },
    IniciarSesion() {
      this.$router.push("/login");
    },
    CambiarPass() {
      if (this.ValidaFormulario()) {
        let url =`${Constantes.rutaTramite}acceso/modifica-pass`;
        let usuarioPlataformaRequest = {};
        usuarioPlataformaRequest.hash = this.hash;
        usuarioPlataformaRequest.clave = this.nuevaClave;

        axios
          .post(url, usuarioPlataformaRequest)
          .then(response => {
            if (response.data.success) {
              this.$swal({
                icon: "success",
                text: "La clave fue generada con éxito"
              });
              this.styleDisplayCambio = "none";
              this.styleBtnIniciarSesion = "";
              this.styleDisplayCorrecto = "";
            } else {
              this.styleDisplayNoExiste = "";
            }
          })
          .catch(e => console.log(e));
      } else {
      }
    },
    ValidaHash() {
      var hash = this.$route.params.hash;
      var url = Constantes.rutaTareasComunes + "valida-recuperacion/" + hash;
      axios
        .get(url)
        .then(response => {
          if (response.data.success) {
            this.hash = hash;
          } else {
            this.styleDisplayCambio = "none";
            this.styleDisplayNoExiste = "";
          }
        })
        .catch(e => console.log(e));
    }
  }
};
</script>



